/**
 * Set fluid units on the root for fully responsize everything.
 *
 * @see https://websemantics.uk/tools/responsive-font-calculator/
 */
:root {
  --lw--fluid-root: calc(0.9375rem + ((1vw - 4px) * 0.1974));
  font-size: var(--lw--fluid-root);
}

* {
  box-sizing: border-box;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

/* placeholders */
::placeholder {
  opacity: 1; /* Chrome, Firefox, Opera, Safari 10.1+ */ /* Firefox */
  color: var(--lw_grey-light);
}

::-moz-placeholder {
  opacity: 1; /* Chrome, Firefox, Opera, Safari 10.1+ */ /* Firefox */
  color: var(--lw_grey-light);
}

:-ms-input-placeholder {
  opacity: 1; /* Chrome, Firefox, Opera, Safari 10.1+ */ /* Firefox */
  color: var(--lw_grey-light);
}

:-ms-input-placeholder {
  color: var(--lw_grey-light);
}

::-ms-input-placeholder {
  color: var(--lw_grey-light); /* Microsoft Edge */
}

/* browser scrollbar */
:root {
  /* browser scroll behaviour */
  -ms-scroll-snap-type: y proximity;
  scroll-snap-type: y proximity;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 0.5em;
}

body::-webkit-scrollbar-track {
  background: #000;
}

body::-webkit-scrollbar-thumb {
  border-radius: 0.25em;
  background-color: #666;
}

:focus-visible {
  outline: -webkit-focus-ring-color auto 1px;
}

select {
  text-transform: none;
}

input {
  overflow: visible;
}

input,
optgroup,
select,
textarea {
  line-height: inherit;
  font-family: inherit;
  font-size: inherit;
}

input:hover:not(:disabled):not(.disabled),
optgroup:hover:not(:disabled):not(.disabled),
select:hover:not(:disabled):not(.disabled),
textarea:hover:not(:disabled):not(.disabled) {
  cursor: pointer;
}

html,
body {
  min-height: 100vh;
}

.wp-site-blocks {
  display: flex;
  flex-direction: column;
}

footer {
  margin-block-start: auto !important;
}

.wp-block-spacer {
  margin-block-start: 0 !important;
}

:where(body .is-layout-grid),
:where(body .is-layout-flex) {
  gap: var(--wp--custom--spacing--gutter--medium) var(--wp--custom--spacing--gutter--small);
}

/*

Trying "useRootPaddingAwareAlignments": true, in theme.json.

// Default page padding.
.wp-site-blocks > *,
.editor-styles-wrapper > *:not( .edit-post-visual-editor__post-title-wrapper ) { // Include the editor.
	padding-top: 0;
	padding-bottom: 0;
	padding-left: var(--wp--custom--spacing--gutter--medium);
	padding-right: var(--wp--custom--spacing--gutter--medium);

	// If the block has alignment children, don't apply l/r padding...
	&:has( .alignfull, .alignwide ) {
		padding-left: 0;
		padding-right: 0;

		// ..because it will be handled by the child.
		:is( .alignfull, .alignwide ):not( :has( .alignfull, .alignwide ) ) {
			padding-left: var(--wp--custom--spacing--gutter--medium);
			padding-right: var(--wp--custom--spacing--gutter--medium);
		}
	}
}
*/
