/*========================================================= 1. TYPOGRAPHY ====*/
/*---------------------------------------------------- 1.1 Text Gradients ----*/
.textGradient-sunrise {
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  background-color: #fbad40;
  background-image: linear-gradient(170deg, #fbad40 0%, #ef4236 100%);
}

/*============================================================= 2. BLOCKS ====*/
/*--------------------------------------------------- 2.1 camperAnimation ----*/
@media (max-width: 500px) {
  .camperAnimation_wrapper > .wp-block-group {
    background: linear-gradient(transparent 30%, rgba(0, 0, 0, 0.4666666667) 80%);
    min-height: 100%;
  }
}

